.contacts__group {
  max-width: 500px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 125px;
}

.contacts__block {
  position: relative;
  width: 110px;
  height: 115px;
  background-image: url("../img/sbg.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.social-link {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 10px;
}

.icon-social {
  width: 45px;
  height: 45px;
  fill: transparent;
  stroke: #fff;
  transition: stroke 0.3s ease;
}

.social-link:hover .icon-social {
  stroke: #000;
}


@media (max-width: 576px) {
  .contacts__group {
    margin-top: 80px;
  }
}
