.quote {
  background-color: var(--main-color);
  padding-top: 35px;
  padding-bottom: 35px;
}

.quote__wrap {
  display: flex;
  align-items: flex-start;
  max-width: 580px;
  width: 100%;
  margin: 0 auto;
}

.quote__icon {
  width: 35px;
  height: 35px;
  margin-right: 15px;
}

.quote__text {
  font-family: El Messiri;
  font-style: normal;
  font-weight: 500;
  font-size: 28px;
}
