.consultation {
  padding-top: 50px;
  padding-bottom: 100px;
}

.title {}

.person__name {}

.consultation__content {}

.consultation .row {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}

.column {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.column__block {
  height: 100%;
  background-color: #fff;
  padding: 20px 25px 40px 25px;
  margin-bottom: 25px;
  border-radius: 15px;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.2);
}



.column__title {
  font-weight: 500;
  font-size: 22px;
  text-align: center;
  color: inherit;
  margin-bottom: 35px;
}

.column__text {
  text-align: center;
  font-weight: 400;
  font-size: 18px;
}

.column:nth-child(odd) .column__block {
  background-color: var(--main-color);
}

.column:nth-child(odd) .column__block .column__title,
.column:nth-child(odd) .column__block .column__text {
  color: #fff;
}

.column .btn {
  width: 223px;
  height: 54px;
  display: block;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;

  span {
    font-family: Montserrat;
    font-size: 20px;
  }
}


.title--line {
  position: relative;
  margin-bottom: 50px;
}

.title--line::after {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  content: '';
  width: 124px;
  border-top: 2px dashed var(--main-color);
}

@media (max-width: 850px) {
  .consultation .row {
    grid-template-columns: 1fr 1fr;
  }
}

@media (max-width: 768px) {
  .consultation {
    padding-top: 25px;
    padding-bottom: 40px;
  }
}

@media (max-width: 576px) {
  .consultation .row {
    grid-template-columns: 1fr;
  }
}
