.program.consultation {
  padding-bottom: 50px;
}

.program__content {
  position: relative;
  height: 600px;
  margin-top: 100px;
}

.program__text:nth-child(1) {
  top: 0;
  left: 15%;
  max-width: 250px;
}

.program__text:nth-child(2) {
  top: 25%;
  right: 15%;
  max-width: 450px;
}

.program__text:nth-child(3) {
  bottom: 40%;
  left: 15%;
  max-width: 450px;
}

.program__text:nth-child(4) {
  bottom: 0;
  right: 15%;
  max-width: 450px;
}

.program__text {
  display: inline-block;
  position: absolute;
}

.program__text {
  border: 1px dashed #75bbfd;
  font-weight: 500;
  font-size: 25px;
  text-align: center;
  color: #343333;
  border-radius: 15px;
  padding: 15px 25px;
}

.program__line {
  position: absolute;
}

.program__line--1 {
  top: 10%;
  left: 36%;

  img {
    width: 95%;
  }
}

.program__line--2 {
  top: 35%;
  left: 30%;
  transform: rotate(135deg);
}

.program__line--3 {
  bottom: 15%;
  left: 25%;
  width: 95%;
}


@media (max-width: 768px) {
  .program__line--1 {
    width: 25%;
    top: 12%;
    left: 45%;
  }

  .program__line--2 {
    width: 55%;
    top: 40%;
    left: 20%;
  }

  .program__line--3 {
    width: 60%;
    bottom: 20%;
    left: 25%;
  }
}

@media (max-width: 576px) {

  .program__line--1,
  .program__line--2,
  .program__line--3 {
    display: none;
  }
}
