.header {
  height: 80px;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}

.menu {}

.menu__list {
  display: flex;
  align-items: center;
}

.menu__list-item:not(:last-child) {
  margin-right: 30px;
}

.menu__link,
.tel-link {
  text-decoration: none;
  color: #343333;
  font-size: 22px;
  transition: all 0.3s ease;
}

.menu__link {
  font-weight: 500;

  &:hover {
    color: var(--main-color);
  }
}

.header__tel {}

.tel {}

.icon-call {
  width: 23px;
  height: 23px;
  margin-right: 10px;
  transition: all 0.3s ease;
}

.tel-link {
  font-weight: 700;
  display: flex;
  align-items: center;

  &:hover {
    color: var(--main-color);
  }
}

.tel-link:hover .icon-call {
  fill: var(--main-color);
}


@media (max-width: 700px) {
  .menu {
    display: none;
  }

  .header__tel {
    margin: 0 auto;
  }
}
