.welcome {
  padding-top: 50px;
  padding-bottom: 50px;
  width: 100%;
  height: 100%;

}

.welcome__wrapper {
  max-width: 1288px;
  width: 100%;
  margin: 0 auto;
  min-height: 700px;
  background-image: url("../img/bg.png");
  background-position: right center;
  background-size: auto;
  background-repeat: no-repeat;
}

.welcome__content {
  padding-top: 100px;
  padding-bottom: 40px;

}

.title {
  font-family: El Messiri;
  font-weight: 700;

  line-height: 1.2;

}

.welcome .title {
  margin-bottom: 60px;
  font-size: clamp(28px, 5vw, 68px);
  color: var(--main-color);
}

.list {
  margin-bottom: 100px;
}

.list-item:not(:last-child) {
  margin-bottom: 10px;
}

.list-item {
  span {
    margin-left: 15px;
    font-weight: 500;
    font-size: 24px;
  }
}



.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  background: var(--main-color);
  border: var(--btn-border);
  border-radius: var(--btn-radius);
  color: var(--btn-text-color);
  background: var(--btn-bg-color);
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 22px;
  line-height: 1.2;
  transition: all 0.3s ease;

  &:hover {
    cursor: pointer;
    color: var(--main-color);
    background: var(--btn-text-color);
  }
}


.welcome .btn {
  max-width: 414px;
  height: 85px;
}


@media (max-width: 768px) {
  .welcome__wrapper {
    background-image: none;
    min-height: 350px;
  }

  .welcome__content {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .list {
    margin-bottom: 60px;
  }
}

@media (max-width: 576px) {
  .welcome .btn {
    margin: 0 auto;
  }

  .welcome .title {
    margin-bottom: 40px;
    font-size: 32px;
    text-align: center;
    color: var(--main-color);
  }

  .welcome .list-item {
    display: flex;
    flex-direction: column;

    span {
      margin-left: 0;
      text-align: center;
    }

    svg {
      margin: 0 auto;
      margin-top: 7px;
      margin-bottom: 7px;
    }
  }
}

@media (max-width: 500px) {
  .list-item span {
    font-size: 18px;
  }

  .welcome .btn {
    max-width: 350px;
    height: 60px;
    font-size: 18px;
  }

  .person__subtitle {
    font-size: 22px;
  }

  .text {
    font-size: 18px;
  }

}
