.person {
  padding-top: 50px;
  padding-bottom: 50px;
}

.person__inner {
  display: flex;
  align-items: center;
}

.person__img {
  max-width: 400px;
  border-radius: 15px;
  overflow: hidden;
  background-color: #C4C4C4;
}

.avatar {}

.person__content {
  max-width: 750px;
  width: 100%;
  margin-left: 35px;
}

.title {}

.person__name {
  font-size: clamp(28px, 5vw, 50px);
  font-weight: 500;
  margin-bottom: 10px;
  text-align: center;
}

.person__prof {
  font-family: Montserrat;
  font-weight: 400;
  font-size: 22px;
  text-align: center;
  color: var(--main-color);
  margin-bottom: 50px;
  position: relative;
}

.person__prof::after {
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  display: block;
  content: '';
  width: 124px;
  border-top: 2px dashed var(--main-color);
}



.person__subtitle {
  font-weight: 400;
  font-size: 28px;
  text-align: center;
  margin-bottom: 25px;
}

.text {
  font-size: 22px;
  text-align: justify;
}

.person__text:not(:last-child) {
  margin-bottom: 15px;
}

@media (max-width: 850px) {
  .person__inner {
    flex-direction: column;
  }

  .person__img {
    margin-bottom: 25px;
  }

  .person__content {
    max-width: 100%;
    width: 100%;
    margin-left: 0;
  }
}

@media (max-width: 768px) {


  .person__img {
    width: 255px;
    height: 300px;
    margin: 0 auto;
    margin-bottom: 25px;
  }

  .person__content {
    max-width: 100%;
    margin-left: 0;
  }

  .person {
    padding-top: 25px;
    padding-bottom: 25px;
  }
}
