* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
  line-height: 1.2;
  color: #343333;
}

.page__wrapper {
  width: 100%;
  height: 100%;
  background-color: #fbfbfb;
  box-sizing: border-box;
}

.section-stars {
  position: relative;
  max-width: 1258px;
  width: 100%;
  margin: 0 auto;
  z-index: 1;

  &::before {
    position: absolute;
    bottom: 0;
    left: 0;
    content: '';
    width: 150px;
    height: 150px;
    background-image: url("../img/stars1.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    content: '';
    width: 150px;
    height: 150px;
    background-image: url("../img/stars2.png");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
}

.container {
  max-width: 1248px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 0 10px;
  position: relative;
  z-index: 2;
}
